* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1.2rem;
  width: 100vw;
  height: 100vh;
}
main.container {
  width: 50em;
  background-color: greenyellow;
  height: 20em;
  box-shadow: 2px 10px 5px black;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  padding-top: 2em;
  margin: 0 auto;
}
h1.title {
  margin-bottom: 0.5em;
  text-shadow: 5px 5px 5px teal;
}
p.desc {
  margin-bottom: 0.5em;
}
input#date-el {
  width: 15em;
  height: 5em;
  border-radius: 2em;
  border: none;
  outline: none;
  background-color: #ee489d;
}
p#answer-el {
  color: #c0c019;
  width: 15em;
  height: 5em;
}
button#submit-btn {
  display: block;
  margin-top: 1em;
  border: none;
  background-color: aqua;
  color: #c40e6f;
  border-radius: 1em;
  padding: 10px 10px;
  font-weight: 800;
  margin: 2em;
}
